body{
    text-align: center;
    background-color:#fcebeb;
    margin: 0px;
 
}
.nav_bar{
    height:80px;
    width:100%;
    background-color: #f8c9c9;
    position: sticky;
    position: relative;
    box-shadow: 2px 2px 5px 1px #f67898;

    
}

a{
    text-decoration: none;
    margin-right: 30px;
    text-align: center;
    color: rgb(59, 8, 30);
    font-size: 23px;
    padding: 10px;
  

}
nav a.active{
    text-decoration: none;
    font-weight: bold;
    background-color:#d52852;
    padding:10px;
 
  }
#heading{
    font-size: 40px;
    color:rgb(7, 33, 112);
    text-shadow: -1px 0 black, 0 1px black, 1px 0 black, 0 -1px black;

}

#count{
    font-size: 23px;
    color:rgb(198, 115, 6);
    text-shadow: 2px 2px 5px #c5e564;
}
#foodicon{
    width: 110px;
    height: 80px;
    margin-right: 0;
    position: absolute;
    top: 0px;
    left:5px;
}
input[type=text] {
    width: 24%;
    box-sizing: border-box;
    border: 2px solid #eb9090;
    border-radius: 4px;
    font-size: 16px;
    background-color:pink;
    background-image: url('https://cdn3.iconfinder.com/data/icons/feather-5/24/search-512.png');
    background-size: contain;
    background-position: 3px 0px; 
    background-repeat: no-repeat;
    padding: 12px 20px 12px 40px;
   

    
  }
  
  input[type=text]:focus {
    background-color: #f7adad;
  }
.Meals_Data{
    display: grid;
    grid-template-columns: 1fr 1fr  1fr ;
    gap: 1rem;
    text-align: center;
    margin-top: 60px;
  
 
}
.Meals_Data li{
    list-style-type: none;
    color:rgb(100, 50, 4);
    text-shadow: 2px 2px 4px #c1a6a6;
    transition: width 2s;
    list-style-type: none;
}
#foodimg:hover{
   border: 3px solid rgb(238, 16, 82);
}
#foodimg{
    width: 300px;
    max-width: 400px;
    max-height: 250px;
    border: 3px solid brown;
    border-radius: 5%;
   
}
#msg{
    text-align: center;
    color: #7a0a62;
}

/* Footer */
#footer{
    width:100%;
    height:auto;
    background-color: #e08a8a;
   margin-top: 500px;
}
#copy{
    letter-spacing: 1px;
    font-size: 18px;
}

#btn_1{
    width:110px;
    height:50px;
    border-radius: 10px;
    background-color: #d295f8;
    color:#7a0a62;

}
/* ----------------DishesPage------------------- */
.Dishes_Data{
    list-style-type: none;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    color: #d52852;
}
#heading_1{
    font-size: 40px;
    color:rgb(100, 50, 4);
    text-shadow: -1px 0 black, 0 1px black, 1px 0 black, 0 -1px black;
}


#items{
    color:#674b04;
    border:3px solid brown;
    box-shadow: 1px 1px 6px 1px #c39b37;
    border-radius: 20px;
    font-size: 30px;
    background-color: #f8cccc;
    
}
#items:hover{
    background-color: burlywood;
}

#dish_items{
    font-size: 25px;
    font-family:Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
    line-height: 40px;
    color:brown;
    padding:30px;
    margin: 30px;
    border:2px solid #b17070;
    box-shadow:2px 2px 8px 2px rgb(192, 115, 115);
    
}
#detailheading{
    color:#d52852;
    font-size: 50px;
}



@media screen and (min-width:269px) and (max-width:920px)
{
    .Meals_Data{
        gap:2rem;
        grid-template-columns: 1fr  ;
    }
    #foodimg{
       max-width:100%;
       height: auto;
       
    }
    input[type=text] {
        width: 20%;
      }
}